export class User {
    constructor() {
    }

    id: number | null;
    username: string | null;
    password: string | null;
    headimg: string | null;
    authorities: string[] | null;
    phone: string | null;
    weixin: string | null;
    qq: string | null;
    realName: string | null;
    consumePoint: number;
}
export function buildUser(user: User, userdata: any) {
    user.id = userdata['id'];
    user.username = userdata['username'];
    user.headimg = userdata['headimg'];
    user.phone = userdata['phone'];
    user.weixin = userdata['weixin'];
    user.qq = userdata['qq'];
    user.realName = userdata['realName'];
    user.consumePoint = userdata['consumePoint'];

}
export const USER: User = {
    id: null,
    username: null,
    password: null,
    authorities: null,
    headimg: 'assets/img/default_head',
    phone: null,
    weixin: null,
    qq: null,
    realName: null,
    consumePoint: 0,
};
export const RSA_PRIVATE_KEY = 'ss';



